<template>
  <div class="community">
    <div class="header">社区推荐</div>
    <div class="loading" v-if="showLoading">
      <van-loading type="spinner" color="#6b6b6b" />
    </div>
    <div class="card-list">
      <CommunityCard
        v-for="(item, index) in CommunityCardArr"
        :item="item"
        :key="index"
        @CommunityCardClick="CommunityCardClick($event)"
      ></CommunityCard>
    </div>
    <div v-if="!showLoading" class="end" @click="getMoreCommunityCard">
      <div class="loading-more" v-if="isLoadingMore">
        <van-loading type="spinner" color="#6b6b6b" />
      </div>
      <div class="text" v-else>{{EndText}}</div>
    </div>
  </div>
</template>

<script>
import CommunityCard from "@/components/CommintyCard";
export default {
  name: "community",
  data() {
    return {
      CommunityData: {},
      showLoading: false,
      nextPageUrl: null,
      EndText: `加载更多`,
      isLoadingMore: false
    };
  },
  methods: {
    getMoreCommunityCard() {
      this.isLoadingMore = true;
      if (this.nextPageUrl) {
        this.axios
          .get(`https://api.kele8.cn/agent/${this.nextPageUrl}`)
          .then(res => {
            for (const key in res.data.itemList) {
              this.CommunityCardArr.push(res.data.itemList[key]);
              this.nextPageUrl = res.data.nextPageUrl;
            }
          })
          .finally(() => {
            this.isLoadingMore = false;
          });
      } else {
        this.EndText = `到底咯~~~~`;
      }
    },
    CommunityCardClick($event) {
      this.$router.push("/commintycarddetail");
      this.$store.commit("setShowBottomBar", false);
      this.$store.commit("setCommintyCardDetailData", $event);
    }
  },
  computed: {
    CommunityCardArr() {
      return this.CommunityData.itemList;
    }
  },
  watch: {
    CommunityCardArr() {
      if (this.CommunityCardArr) {
        this.showLoading = false;
      }
    }
  },
  created() {
    this.showLoading = true;
    this.axios
      .get(
        `https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v5/index/tab/ugcSelected`
      )
      .then(res => {
        this.CommunityData = res.data;
        this.nextPageUrl = res.data.nextPageUrl;
      });
  },
  components: {
    CommunityCard
  }
};
</script>

<style lang="less" scoped>
.community {
  padding: 50px 10px 53px 10px;

  .loading {
    height: 30px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    left: 0;
    right: 0;
    text-align: center;
  }
  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    font-weight: bold;
    background-color: #fafafa;
    font-size: 15px;
    padding: 10px 0;
    z-index: 1;
  }
  .card-list {
    column-count: 2;
    column-gap: 5px;
    .community-card {
      break-inside: avoid;
    }
  }

  .end {
    font-size: 17px;
    font-weight: bold;
    text-align: center;
    color: #6b6b6b;
    padding: 20px 0;
  }
}
</style>