<template>
  <div class="community-card" @click="$emit('CommunityCardClick',item)">
    <div class="img">
      <van-image v-lazy="imgUrl" :src="imgUrl">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
        <template v-slot:error>加载失败</template>
      </van-image>
    </div>
    <div class="description" v-if="Description">{{Description}}</div>
    <div class="author">
      <div class="author-icon">
        <img :src="authorIcon" alt />
      </div>
      <div class="author-name">{{authorName}}</div>
      <div class="collection">
        <span>{{collection}}</span>
        <van-icon name="like-o" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CommunityCard",
  props: ["item"],
  computed: {
    imgUrl() {
      return this.item.data.content.data.cover.detail;
    },
    Description() {
      return this.item.data.content.data.description;
    },
    authorIcon() {
      return this.item.data.header.icon;
    },
    authorName() {
      return this.item.data.header.issuerName;
    },
    collection() {
      return this.item.data.content.data.consumption.collectionCount;
    }
  }
};
</script>

<style lang="less" scoped>
.community-card {
  padding-bottom: 20px;
  width: 100%;
  .img {
    border-radius: 5px;
    overflow: hidden;
    .van-image{
      display:block;
      img {
      width: 100%;
      display: block;
    }
    }
    
  }
  .description {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 14px;
    margin: 10px 0;
    color: #333;
  }
  .author {
    margin: 10px 0;
    display: flex;
    .author-icon {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        display: block;
      }
    }
    .author-name {
      flex: 1;
      color: #6b6b6b;
      font-size: 14px;
      margin-left: 5px;
    }
    .collection {
      display: flex;
      line-height: 20px;

      span {
        flex: 1;
        margin-right: 5px;
        color: #6b6b6b;
        font-size: 12px;
        line-height: 20px;
        display: inline-block;
      }
      i {
        line-height: 20px;
      }
    }
  }
}
</style>